





















import Vue from 'vue';

export default Vue.extend({
  name: 'Playerbox',
  props: {
    player: Object,
  },
  methods: {
    onSelectSession(uid: number) {
      this.$store.dispatch('session/selectSession', uid);
    },
  },
  computed: {
    currentSessionId(): number {
      return this.$store.state.session.currentSessionId;
    },
  },
});
