













import Vue from 'vue';
import moment from 'moment';

export default Vue.extend({
  name: 'MessageInput',
  data() {
    return {
      textarea: '',
    };
  },
  methods: {
    onSubmit() {
      if (this.textarea !== '') {
        this.$store.dispatch('session/sendMessage', this.textarea);
        this.textarea = '';
      }
    },
  },
});
