



























import Vue from 'vue';

export default Vue.extend({
  name: 'PlayerInfo',
  computed: {
    playerName(): string {
      const sid = this.$store.state.session.currentSessionId;
      if (sid) {
        return this.$store.state.user.users[sid].name;
      }
      return '先选择一位玩家';
    },
  },
});
